import React from "react";
import Accordion from "@material-ui/core/Accordion";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { Component } from "react";


class SettingsPage extends Component{

  state = {

    panel1: true,
    panel2: false,
    panel3: false,
    panel4: false,

  };


  handleChange = (panel) => (event) => {
    
    let obj = {};
    obj[panel] = !this.state[panel];
    this.setState(obj);

  };

  render() {

    const classes = {

        root: {
            width: "100%"
          },
          heading: {
            //fontSize: theme.typography.pxToRem(15),
            flexBasis: "33.33%",
            flexShrink: 0
          },
          secondaryHeading: {
            // fontSize: theme.typography.pxToRem(15),
            // color: theme.palette.text.secondary
          }
    };
    return (

        <div className={classes.root}>
          <Accordion
            expanded={this.state.panel1}
            onChange={this.handleChange("panel1")}
          >
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1bh-content"
              id="panel1bh-header"
            >
              <Typography className={classes.heading}>About</Typography>
              <Typography className={classes.secondaryHeading}>
                ...          
                </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
              Grocery application allows you to easy find groceries at different South African shops. Grocery can also save you money by finding similar products for less prices. 
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion
            expanded={this.state.panel2}
            onChange={this.handleChange("panel2")}
          >
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel2bh-content"
              id="panel2bh-header"
            >
              <Typography className={classes.heading}>Privacy Policy</Typography>
              <Typography className={classes.secondaryHeading}>...</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                <a href="" onclick="ViewPricacyPolicy();">
                  {" "}
                  Click to view our privacy policy document.{" "}
                </a>
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion
            expanded={this.state.panel3}
            onChange={this.handleChange("panel3")}
          >
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel3bh-content"
              id="panel3bh-header"
            >
              <Typography className={classes.heading}>Developer</Typography>
              <Typography className={classes.secondaryHeading}>
                ...
              </Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                Grocery is a South African application developed by Sology group.
              </Typography>
            </AccordionDetails>
          </Accordion>
          <Accordion
            expanded={this.state.panel4}
            onChange={this.handleChange("panel4")}
          >
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel4bh-content"
              id="panel4bh-header"
            >
              <Typography className={classes.heading}>Enquiries</Typography>
            </AccordionSummary>
            <AccordionDetails>
              <Typography>
                For enquiries,{" "}
                <a
                  href="mailto:msamaann@gmail.com?Subject=Grocery:Enquiry"
                  target="_top"
                >
                  Send an Email
                </a>
              </Typography>
            </AccordionDetails>
          </Accordion>
        </div>
      );
  }

}


export default SettingsPage;