import React, { Component } from "react";
import '../Libraries/Fontello/css/fontello.css'
import '../Libraries/Fontello/css/animation.css'
import '../Libraries/Fontello/css/fontello-codes.css'
import '../Libraries/Fontello/css/fontello-embedded.css'
import '../Libraries/Fontello/css/fontello-ie7.css'
import '../Libraries/Fontello/css/fontello-ie7-codes.css'
import Switch from '@material-ui/core/Switch';
import Grid from '@material-ui/core/Grid';
import { withStyles } from '@material-ui/core/styles';


const AntSwitch = withStyles((theme) => ({
    root: {
      width: 32,
      height: 21,
      padding: 0,
      display: 'flex',
      borderBottom: "1px solid #a0a0a0",
      borderRadius: 11,
    },
    switchBase: {

      padding: 2,
      color: theme.palette.grey[500],
      '&$checked': {
        transform: 'translateX(12px)',
        color: theme.palette.common.white,
        '& + $track': {
          opacity: 1,
          backgroundColor: theme.palette.primary.main,
          borderColor: theme.palette.primary.main,
        },
      },
    },
    thumb: {
      width: 17,
      height: 17,
      boxShadow: 'none',
    },
    track: {
      border: `1px solid ${theme.palette.grey[500]}`,
      borderRadius: 21 / 2,
      opacity: 1,
      backgroundColor: theme.palette.common.white,
    },
    checked: {

      borderBottom: "none",

    },
  }))(Switch);

class ToggleButton extends Component {

  state = {
    checked: this.props.checked
  }
    
  render() {

    let style = {
      padding: "0 10px 0 15px"
    }

    return(
        <Grid style={style} component="label"  container alignItems="center" spacing={2}>
            <Grid item>
            <AntSwitch  checked={this.props.checked} onChange={this.onChange} name="checkedC" />
            </Grid>
            <Grid item>{this.props.text}</Grid>
        </Grid>
  
    );

  }

  onChange = () => {
    //this.setState({checked: !this.props.checked});
    this.props.onChange();
  }

}
export default ToggleButton;
