import React, { Component } from "react";
import './NavItem.css' 
import '../Libraries/Fontello/css/fontello.css'
import '../Libraries/Fontello/css/animation.css'
import '../Libraries/Fontello/css/fontello-codes.css'
import '../Libraries/Fontello/css/fontello-embedded.css'
import '../Libraries/Fontello/css/fontello-ie7.css'
import '../Libraries/Fontello/css/fontello-ie7-codes.css'
import { NavLink} from "react-router-dom";
class NavItem extends Component {
 

  render(){

    return (
      <NavLink exact to={this.getLinkTo}  activeClassName="active" className="navItem"
        title={this.props.name}  > 
          <i className={this.props.iconClass}></i> <span className="navText"> {this.props.name}</span>
  
        </NavLink>
    )
  };

    
  getLinkTo = () =>{

    let path = (this.props.name.toLowerCase() === "home")? "" : this.props.name; // leave it empty if home >> ex: /

    return "/" + path; // ex: /Search 
  }

}

export default NavItem;
