import React, { Component } from "react";
import styles from './ListItem.module.css';

import { SwipeableList, SwipeableListItem } from '@sandstreamdev/react-swipeable-list';
import '@sandstreamdev/react-swipeable-list/dist/styles.css';

class ListItem extends Component {
 

  render(){

    return (
        
        //SwipeableList
        //Documentation: https://github.com/sandstreamdev/react-swipeable-list
        //Example: https://sandstreamdev.github.io/react-swipeable-list/
        <SwipeableList>
            <SwipeableListItem
                swipeLeft={{
                content: <div className={styles.swipeBox + " " + styles.swipeToLeft}>
                    <div className={styles.content}>
                        <div className={styles.listIcon + " icon icon-plus-squared-1"}></div>
                        <div className={styles.swipText}>Save</div>
                        </div>
                    </div>,
                action: () => console.info('swipe action triggered')
                }}
                swipeRight={{
                content: <div className={styles.swipeBox + " " + styles.swipeToRight}>
                    <div className={styles.content}>
                        <div className={styles.listIcon + " icon icon-trash-1"}></div>
                        <div className={styles.swipText}>Remove</div></div>
                    </div>,
                action: () => console.info('swipe action triggered')
                }}
                onSwipeProgress={progress => console.info(`Swipe progress: ${progress}%`)}
            >
                

                
            <div className={styles.item}>
                <div className={styles.leftSide}><img  src={this.props.image} alt="img"/><div className={styles.imageText}></div></div> 
                
                <div className={styles.rightSide}>
                    <div className={styles.text}> {this.props.text} <img className={styles.logo} src={this.props.logo}  alt="img"/></div>
                    <div >{this.props.hint}</div>
                </div>
            </div>
 
        </SwipeableListItem>
        </SwipeableList>
        
    )
  };



}

export default ListItem;
