import { Component } from 'react';
import './App.css';
import NavBar from './Components/NavBar';
import HomePage from './Components/Page_Home';
import ListsPage from './Components/Page_Lists';
import ProfilePage from './Components/Page_Profile'
import SettingsPage from './Components/Page_Settings'
import MainLoaderPage from './Components/Page_MainLoader'
import Shops from './Libraries/Grocery/Shops.js';

import {
  BrowserRouter as Router,
  Switch,
  Route,
 
} from "react-router-dom";
class App extends Component{


  // constructor(props){
  //   super(props);
    
  // }

  state = {
    currentPage: null,
    shopsReady: false,
    navItems: [
      {id: 1, name: "My Lists", iconClass: "icon-basket-1"},
      {id: 2, name: "Home", iconClass: "icon-home-1"},
      {id: 3, name: "Profile", iconClass: "icon-user-1"},
    ]
  }


  render() { return (

      <Router>
      
        <div className="page" >
          <Switch>       
            <Route exact path="/"> { (this.state.shopsReady)? <HomePage /> : <MainLoaderPage />} </Route>
            <Route path="/Profile"> <ProfilePage /> </Route>
            <Route path="/My Lists"> <ListsPage /> </Route>
            <Route path="/Settings"> <SettingsPage /> </Route>
          </Switch>
        </div>
        
        <NavBar navItems={this.state.navItems} />

      </Router>

    );
  }

  componentDidMount = () =>{
    Shops.Prepare(
      () => {

        setTimeout(() => {
          this.setState({shopsReady: true});

        }, 1000);
      }
    );        
}

}

export default App;
