import React, { Component } from "react";
// import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import Loader from './Loader';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
// import LocalMall from '@material-ui/icons/LocalMall';
import MainLogo from '../logo.svg'

class MainLoaderPage extends Component {


    render() {


        const classes = {

            root:{
                    backgroundColor:"#009999", 
                    height:"100%", 
                    width:"100%", 
                    zIndex: "99999999", 
                    position: "fixed", 
                    textAlign: "center"
            },
            sub:{
                marginTop: "50px",
                color: "white",
            },
            icon:{
                color: "white",
                fontSize: 200
            }
            
        }

       
        return(
            <div style={classes.root}>
            
                <div style={classes.sub} onClick={this.goToHome} >

                    <div class="" />
                    <img src={MainLogo} height="200" width="200" />
                    {/* <ShoppingCartIcon style={classes.icon} /> */}

                    <Loader color="white" />
                </div>
                

            </div>
                
        );

    }


}


export default MainLoaderPage;
