import Ajax from '../Ajax.js';


const Shop = function(shopObject){

    this.Name = shopObject.Name;
    this.Logo = shopObject.Logo;
    this.Username = shopObject.Username;
    this.Website = shopObject.Website;
    this.Active = true;

    this.ToggleStatus = () => {
        this.Active = !this.Active;
    }
}


var Shops = {
    List: [],
    Prepare: function(onReady){
        let request = {
            "Body": {
            "ActionName": "GetAllShops",
            "Country": "South Africa"
            }
        };

        let onSuccess = (data)=> {

            let shopsList = data.Result;
            
            for(let i = 0; i<shopsList.length; i++){

                let shop = new Shop(shopsList[i]);
                Shops.List.push(shop);
            }
            //window.location.href = "/Home";
            
            if(onReady) onReady();
        }

        Ajax.Post(request, onSuccess);
    }
};


export default Shops;