import React, { Component } from "react";
import Tip from './Tip';
import Shops from '../Libraries/Grocery/Shops.js';
import Ajax from '../Libraries/Ajax.js';
import ListItem from "./ListItem";
import SmallList from "./SmallListItem";
import SearchBox from './SearchBox';
import Loader from './Loader';

class HomePage extends Component {

    state = {
        searchResult: null // array of search results (products)
    }

    render() {
        
        return(
        <div>
            <SearchBox hint="Search.." onSubmit={this.searchAll} />

            <div>
                {(!this.state.searchResult)? 
                
                
                <SmallList />

                : 
                
                this.state.searchResult}
                
            </div>
        </div>
        );

    }

    
  searchAll = (searchTerm) => {

    this.searchResult = [];
    
    this.setState({
      searchResult:  <Loader />
    });
    


    for(let shop of Shops.List){
      if(shop.Active) this.search(shop.Username, searchTerm);
    }

  }
  searchResult = [];
  search = (shopName, searchTerm) => {
    
    let homePage = this;
    let request = {
      "Body": {
          "ActionName": "SearchProductsByShop",
          "Search": searchTerm,
          "Shop": shopName
      }
    };

    let onSuccess = (data) => {

      if (data.Result.length !== 0) {
        
          for (let i = 0; i < data.Result.length; i++) {

            let currentRecord = data.Result[i];
             

            let product = {
                  key: i,
                  selected: false, // when the item clicked
                  name: currentRecord.Name,
                  image: currentRecord.Image,
                  orgPrice: currentRecord.OrgPrice,
                  price: currentRecord.Price,
                  promoText: currentRecord.PromoText,
                  textPrice: "",
                  logo: currentRecord.ShopLogo,
                  description: currentRecord.Description,
                  barcode: currentRecord.Barcode,
                  //Url: Grocery.CleanUrl(currentRecord.Url),
                  //ShopUsername: shop.Username,
                  weight: null,
                  searchTerm: searchTerm, // the search term used to get this product 
                  //LastUpdated: Util.Time.Date() // last updated time
              }
              homePage.searchResult.push(product);

              
          }
          
          homePage.setState({
            searchResult: homePage.searchResult.map(item =>             
              <ListItem key={item.key} text={item.name} hint={item.price}
              image= {item.image}
              logo= {item.logo} />
              )
          });

          //searchComponent.props.searchPage.showContent(searchResult);

      } else {
        
        homePage.setState({searchResult:
        <Tip tip="We found nothing " iconClass="icon-emo-unhappy"/>});
        console.info("Not Found");
          //console.info("No products from " + shop.Username);
      }

    }
    var onError = function(){
                      
    }

    Ajax.Post(request, onSuccess, onError);

  }
}
export default HomePage;
