import React, { Component } from "react";
import classes from './SearchBox.module.css';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

import clsx from "clsx";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import SettingsIcon from "@material-ui/icons/Settings";
import ToggleButton from './ToggleButton';
import Shops from '../Libraries/Grocery/Shops.js';
import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import { NavLink} from "react-router-dom";

class Menu extends Component {
 

  // constructor(props){
  //   super(props);
   
  // }

  state = {
    active: false,
    ShopsList: Shops.List
  }

  render(){

    const styles = {
      width: "400px"
    }

    return (


      <div>

        <IconButton className={classes.iconButton} onClick={this.toggleDrawer(true)} aria-label="menu">
          <MenuIcon />
        </IconButton>

          <Drawer style={styles} anchor="right" open={this.state.active} onClose={this.toggleDrawer(false)} >
            
             {this.list("right")}
             
          </Drawer>
      </div>


    )
  };

  toggleDrawer = (open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    this.setState({ active: open });// ...this.state,
  };

  list = () => (
    <div
      className={clsx(classes.list, false)}
      role="presentation"
      //onClick={this.toggleDrawer(false)}
      onKeyDown={this.toggleDrawer(false)}
    >
      <List>

      <NavLink exact to={"/Settings"} > 
                <ListItem button key={"Settings"}>
            <ListItemIcon>
              <SettingsIcon />
            </ListItemIcon>
            <ListItemText primary={"Settings"} />
          </ListItem>
        </NavLink>


          <ListItem button key={"List"}>
            <ListItemIcon>
              <ShoppingBasketIcon />
            </ListItemIcon>
            <ListItemText primary={"List"} />
          </ListItem>



      </List>
      <Divider />
      <List>
        {
          this.state.ShopsList.map((shop, index) => (

            <ToggleButton key={index} checked={shop.Active} onChange={()=>{this.toggleShopStatus(shop)}} text={shop.Name} />
          ))

        }
        
      </List>
    </div>
  );

  toggleShopStatus = (shop) =>{

    shop.ToggleStatus();//toggle active

    //let newShopsArray = this.state.ShopsList.slice(0);//clone array 
    this.setState({ShopsList: Shops.List});
  }

  handleOnChange = (ev) => {
  }

  componentDidMount(){
  }

}

export default Menu;
