var Ajax = {

    Post: function(jsonRequest, onSuccess, onFailed){

        try{

            jsonRequest.Access = {
                Name: "Grocery",
                Token: ".-jdka)shj$$dh#@j%!!ahdjas*dj(ka+s/??>dasdas<da&!!==||/~",
            }
    
            let url = "https://localhost:666";//"https://api.sology.net";//
            fetch(url, {
            method: 'post',
            headers: {
                'Accept': 'application/json, text/plain, */*',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(jsonRequest)
            }).then(res => res.json())
            .then(res => {if(onSuccess) onSuccess(res)});

            
        }catch(error){
            if(onFailed) onFailed();
        }
        
    }
}

export default Ajax;
