import React, { Component } from 'react';
import styles from './SmallListItem.module.css';
import { SwipeableList, SwipeableListItem } from '@sandstreamdev/react-swipeable-list';
import '@sandstreamdev/react-swipeable-list/dist/styles.css';

class SmallList extends Component {

    render() { 
        return ( <SwipeableList>
            <SwipeableListItem
                swipeLeft={{
                content: <div className={styles.swipeBox + " " + styles.swipeToLeft}>
                    <div className={styles.content}>
                    <div className={styles.swipeText}>Save</div>
                    <div className={styles.listIcon + " icon icon-plus-squared-1"}></div>
                    </div>
                    </div>,
                action: () => console.info('swipe action triggered')
                }}
                swipeRight={{
                content: <div className={styles.swipeBox + " " + styles.swipeToRight}>
                    <div className={styles.content}>
                    <div className={styles.listIcon + " icon icon-trash-1"}></div>

                    <div className={styles.swipeText}>Remove</div>
                        
                        </div>
                    </div>,
                action: () => console.info('swipe action triggered')
                }}
                onSwipeProgress={progress => console.info(`Swipe progress: ${progress}%`)}
            >
                
                <div className={styles.smallListItem}>

            <div><i className="icon-history"></i> test</div>

            <div className={styles.hint}>2 Hours ago</div>
            </div>
      
 
        </SwipeableListItem>
        </SwipeableList>
          
     
         );
    }
}
 
export default SmallList;
